<template>
  <div @mouseover="mouseEnter" @mouseout="mouseLeave">
    <b-carousel-list
      style="box-shadow: none"
      icon-size="is-large"
      v-model="test"
      :data="items"
      :items-to-show="itemsToShow"
      :items-to-list="itemsToShow"
    >
      <template v-slot:item="list">
        <div class="card px-1">
          <div style="border-radius: 3rem; overflow: hidden" class="card-image">
            <figure class="image is-4by3">
              <a @click="info(list.index)"><img :src="list.image" /></a>
            </figure>
          </div>
          <div class="card-content">
            <div class="content px-0 has-text-centered">
              <h2 class="title is-4 is-primary">{{ list.title }}</h2>
              <p v-html="list.content" class="pFontSize"></p>
            </div>
          </div>
        </div>
      </template>
    </b-carousel-list>
  </div>
</template>

<style lang="scss">
@import "../variable";
.is-primary {
  color: $primary;
}
.pFontSize {
  font-size: 4vw;
}
@media all and (min-width: 768px) {
  .pFontSize {
    font-size: 1.4vw;
  }
}
</style>

<script>
export default {
  props: {
    itemsToShow: {
      default: 1,
      type: Number,
    },
    sec: {
      default: 10,
      type: Number,
    },
  },
  data() {
    return {
      isMouseHover: false,
      test: 0,
      items: [
        {
          title: "線上約診",
          image: require("../assets/線上約診.jpg"),
          content: "預約台兒篩檢、<br>新生兒篩檢。",
        },
        {
          title: "胎兒檢查",
          image: require("../assets/胎兒檢查.jpg"),
          content: "根據媽媽的妊娠週數，<br>規劃適合的胎兒篩檢。",
        },
        {
          title: "新生兒檢查",
          image: require("../assets/新生兒檢查.jpg"),
          content: "依照寶寶的年齡，<br>規劃適合的評估篩檢。",
        },
        {
          title: "報告瀏覽",
          image: require("../assets/報告瀏覽.jpg"),
          content:
            "在台兒看診的孕媽，<br>註冊並登入媽媽幫，<br>即可看到當天的報告。",
        },
        {
          title: "個人健康",
          image: require("../assets/個人健康.jpg"),
          content:
            "每天記錄自己血壓、<br>體重，了解身體狀態，<br>注意自己是否為子癲前症高危險的一群。",
        },
        {
          title: "衛教專區",
          image: require("../assets/護理指導.jpg"),
          content: "提供產前、產後的專業知識，<br>讓您不再擔心懷孕後的每件事。",
        },
      ],
    };
  },
  mounted() {
    setInterval(() => {
      if (!this.isMouseHover) {
        this.runOnce();
      }
    }, 1000 * this.sec);
  },
  methods: {
    info(value) {
      this.test = value;
    },
    mouseEnter() {
      // alert('ENTER')
      this.isMouseHover = true;
    },
    mouseLeave() {
      // alert('LEAVE')
      this.isMouseHover = false;
    },
    runOnce() {
      // 輪播一次
      this.test = (this.test + this.itemsToShow) % this.items.length;
    },
  },
};
</script>

